const serviseItems = [
  { id: '1', name: ' ' },
  { id: '2', name: ' ' },
  { id: '3', name: ' ' },
  { id: '4', name: ' ' },
  { id: '5', name: ' ' },
  { id: '6', name: ' ' },
  { id: '7', name: ' ' },
  { id: '8', name: ' ' },
  { id: '9', name: ' ' },
  { id: '10', name: '  ' },
];

const serviseEl = document.querySelector('.servise__list');

const makeServise = () => {
  const element = serviseItems
    .map(({ id, name }) => {
      return `      
          <li class="servise__item">
            <p class="servise__title">${name}</p>
          </li>`;
    })
    .join('');
  serviseEl.insertAdjacentHTML('afterbegin', element);
};

makeServise();
