const schudlesContent = [
  { day: '', time: '10:00-19:00' },
  { day: '', time: '10:00-19:00' },
  { day: '', time: '10:00-19:00' },
  { day: '', time: '10:00-19:00' },
  { day: `'`, time: '10:00-19:00' },
  { day: '', time: '  ' },
  { day: '', time: '  ' },
];

const schedlelEl = document.querySelector('.schedule-body');

const makeSchudle = () => {
  const element = schudlesContent
    .map(({ day, time }) => {
      return `  
   <tr>
   <td class="title__td">${day}</td>
   <td>${time}</td>
  </tr>`;
    })
    .join('');
  schedlelEl.insertAdjacentHTML('afterbegin', element);
};

makeSchudle();
